
import wx from 'weixin-js-sdk'
import { mapActions ,mapGetters } from 'vuex'
import Badge from 'vux/src/components/badge/index.vue'

import fooBar from '../components/center'
export default {
name: "person-center",
data(){
return {
}
},
computed:{
...mapGetters({
user:'getUser',
wwc:'getWwc'
})
},
components:{
Badge ,fooBar
},
mounted(){
document.title = '个人中心'
this.$nextTick(() => {


if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}
if(this.$route.query.mallId){
localStorage.setItem('mallId',this.$route.query.mallId+'')
}

this.getUserInfo()
this.getWwcAll()
} )
},
methods:{
...mapActions(['getUser','getWwc1','getWwc2']),
toDa(cs,csi){

wx.miniProgram.navigateTo({
url:'/pages/webview/index?path=myOrder&cs='+ encodeURIComponent(cs)+'&csi='+csi
})
},
toPath(p,q,z){
let fr=''
if(z=='gift'){
fr='?fr=me'
}
if(!q){

wx.miniProgram.navigateTo({
url:'/pages/webview/index?path='+ p
})
}
else{
wx.miniProgram.navigateTo({
url:p+fr
})
}
},
getUserInfo(){
this.getUser({this:this,method:this.getUser,data:{mall_id:localStorage.getItem('mallId')}})
},
getWwcAll(){
this.getWwc1({this:this,method:this.getWwc1})
this.getWwc2({this:this,method:this.getWwc2})
}
}
}



